.ResizableLayout {
  width: 100%;

  .ResizableLayout__header,
  .ResizableLayout__alert {
    position: relative;
    overflow: auto;
    height: 100%;
    width: 100%;
  }

  .ResizableLayout__header {
    flex: 0 0 50px;
  }

  .ResizableLayout__alert {
    flex: 0 0 40px;
  }

  .horizontal {
    > .reflex-splitter {
      height: 8px;
      background:
        transparent
        no-repeat
        center
        center
        url('data:image/svg+xml;base64,');
    }
  }

  .vertical {
    > .reflex-splitter {
      width: 8px;
      background:
        transparent
        no-repeat
        center
        center
        url('data:image/svg+xml;base64,');
    }
  }
}
