@import 'lib/components/Popover/Popover';
@import 'lib/package';

.Dropdown {
  position: absolute !important;
  z-index: $z-index-dropdown;
  width: auto;
  background-color: $color-background-primary;
}

.rc-trigger-popup {
  position: absolute;
  left: -9999px;
  top: -9999px;
  z-index: $z-index-rc-trigger-popup;
}

.Menu.slds-dropdown {
  position: static;
  left: initial;
  transform: none;
  float: none;
  outline: 0;
}
