@import 'lib/package';

.Picklist {
  outline: none;

  .Input {
    overflow: hidden;
    text-overflow: ellipsis;

    &[readonly]::placeholder,
    &::placeholder {
      font-style: italic;
      color: $color-text-tertiary;
    }
  }

  .Menu {
    position: absolute;
    z-index: 20;
  }
}

.slds-has-error .Input.slds-input:active,
.slds-has-error .Input.slds-input:focus {
  box-shadow: $color-border-error 0 0 0 1px inset, 0 0 3px $color-border-brand-primary;
}

/* stylelint-disable max-nesting-depth, selector-max-compound-selectors */
.slds-has-error .Picklist .Search .InputWrapper {
  .Input {
    box-shadow: none;
    border-color: $color-border;
  }

  .Input.slds-input:focus,
  .Input.slds-input:active {
    box-shadow: none;
  }
}
/* stylelint-enable */

// #UI-3669 - Since we add role="combobox" SF adds a border color grey
// which tones down the red border thickness
.slds-has-error .Input.slds-input.slds-combobox__input {
  border-color: $color-border-error;
}
