@import 'lib/package';

.Box {
  position: relative;

  .Box__remove-button {
    position: absolute;
    top: 0;
    right: 0;
    visibility: hidden;
  }

  &:hover .Box__remove-button {
    visibility: visible;
  }
}

.Box--is-being-dragged {
  box-shadow: $shadow-drag;
}

.Box--has-hover-style:hover {
  background-color: $color-background;
}
