@import 'lib/package';

.ColorPicker__overlay {
  display: inline-block;
  background: $color-gray-1;
  border: 0 solid rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
  width: 20rem;
  min-height: 9rem;
  z-index: $z-index-content-inside-modal;

  &::before {
    width: 1rem;
    height: 1rem;
    position: absolute;
    transform: rotate(45deg);
    content: '';
    background-color: inherit;
  }

  .ColorPicker__popover {
    .ColorPicker__container {
      padding: 15px 9px 9px 15px;
    }

    .ColorPicker__swatch {
      display: inline-block;
    }
  }

  /* stylelint-disable selector-class-pattern  */
  &.ColorPicker__overlay-placement-bottomLeft::before {
    left: 1.5rem;
    top: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-bottomRight::before {
    left: auto;
    right: 1rem;
    top: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-top::before {
    left: 50%;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-topLeft::before {
    left: 1.5rem;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-topRight::before {
    right: 1rem;
    bottom: -0.5rem;
    margin-left: -0.5rem;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-right::before {
    left: -0.5rem;
    top: 50%;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-rightTop::before {
    left: -0.5rem;
    top: 1.5rem;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-rightBottom::before {
    left: -0.5rem;
    bottom: 1rem;
    margin-top: -0.5rem;
    box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-left::before {
    right: -0.5rem;
    top: 50%;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-leftTop::before {
    right: -0.5rem;
    top: 1.5rem;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07);
  }

  &.ColorPicker__overlay-placement-leftBottom::before {
    right: -0.5rem;
    bottom: 1rem;
    margin-top: -0.5rem;
    box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.07);
  }
  /* stylelint-enable */
}

.ColorPicker__trigger {
  position: relative;
  width: 30px;
  height: 30px;

  &.ColorPicker__trigger--disabled {
    cursor: not-allowed;
  }
}
