@import 'lib/package';

.Badge {
  margin: 0 3px;
  display: inline-flex;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 89px;
  max-width: 180px;
  text-align: center;
  cursor: default;
  vertical-align: middle;
  align-items: center;

  .Badge__label {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;

    .slds-icon_container { // stylelint-disable-line selector-class-pattern
      vertical-align: top;
    }
  }

  &.Badge--allow-min-width {
    min-width: 50px;
  }

  &.Badge--has-icon.Badge--allow-min-width {
    min-width: 68px;
  }

  &.Badge--allow-min-width.Badge--is-removable {
    min-width: 70px;
  }

  &.Badge--has-icon.Badge--is-removable {
    min-width: 123px;  // 89 + 14 + 20
  }

  &.Badge--has-icon.Badge--allow-min-width.Badge--is-removable {
    min-width: 94px; // 60 + 14 + 20
  }

  &.slds-badge_lightest { // stylelint-disable-line selector-class-pattern
    height: 25px;
    border: 1px solid $color-gray-5;
    background-color: $color-gray-1;
    font-size: 0.75rem;
    text-transform: none;
    letter-spacing: normal;

    &:hover {
      background-color: $brand-light-active;
    }
  }

  .Badge__label--is-removable {
    width: calc(100% - 20px);
  }

  /* custom styles specifically for DCx badges */
  .Badge__label--icon-name-record {
    margin: 0 0 0 -7px;
    width: calc(100% + 7px);

    .Icon__container { // stylelint-disable-line selector-class-pattern
      vertical-align: baseline;
    }

    .Badge__icon {
      margin-right: 0;
    }
  }

  .Badge__close {
    float: right;

    &:focus,
    &:active {
      outline: 0;
      box-shadow: none;
    }

    .Icon {
      position: absolute;
      top: 1px;
      right: -2px;
    }
  }
}

.BadgeList {
  margin-top: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;

  .Badge {
    display: inline-block;
    position: relative;
    margin-left: 0;
    margin-right: 6px;
    margin-bottom: 4px;
  }
}

.BadgeList--stacked {
  margin: 0;
  vertical-align: middle;

  .Badge__stack {
    margin: 0;
  }

  /* here to override an .slds-scope issue */
  .Badge__stack + .Badge__stack {
    margin-left: 0;
  }

  .Badge__stack-1,
  .Badge__stack-2 {
    position: absolute;
    top: 0;
  }

  .Badge__stack-top {
    z-index: $z-index-badge - 1;
  }

  .Badge__stack-1 {
    left: 6px;
    z-index: $z-index-badge - 2;
  }

  .Badge__stack-2 {
    left: 12px;
    z-index: $z-index-badge - 3;
  }

  &.BadgeList--stacked:hover {
    .Badge {
      background-color: $brand-light-active;
    }
  }
}
