@import 'lib/package';

.Search {
  .InputWrapper {
    input::-ms-clear,
    input::-ms-reveal {
      display: none;
    }
  }
}

.Search__container {
  .Menu.slds-dropdown {
    position: absolute;
  }

  .Search {
    // The following overriding rules are fixing inconsistencies in SLDS. For more info, see UI-3908.
    .Search__icon {
      fill: $color-gray-7;
    }

    .Search__clear-button:first-of-type {
      margin-top: -0.4375rem;
      color: $color-gray-9;
    }
  }
}
