// only include the salesforce design variables.
@import '@salesforce-ux/design-system/scss/_design-tokens';

/* UI-4086 Undo / Override the LightItalic font which is no longer used on deployed SLDS */
@font-face {
  font-family: 'Salesforce Sans';
  src:
    url('/assets/fonts/webfonts/SalesforceSans-Italic.woff2') format('woff2'),
    url('/assets/fonts/webfonts/SalesforceSans-Italic.woff') format('woff');
  font-style: italic;
  font-weight: 300;
  font-display: swap;
}

// reference the salesforce colors if needed.
$salesforce-blue: rgb(0, 112, 210);
$salesforce-blue-hover: #005fb2;
$salesforce-default-font-family: 'Salesforce Sans', Arial, Helvetica, sans-serif;
$salesforce-default-font-size: 16px;

/*
  z-index Chart
  @url https://www.lightningdesignsystem.com/design-tokens/#category-z-index

  We need a customized z-index chart because our header needs a higher z-index than the one there.
    $z-index-toast : 10000;
    $z-index-spinner: 9050
    $z-index-modal: 9000;
    $z-index-reminder: 8500;
    $z-index-overlay: 8000;
    $z-index-dropdown: 7000;
    $z-index-dialog: 6000;
    $z-index-popup: 5000;
    $z-index-sticky: 100;
    $z-index-docked: 4;
    $z-index-default: 1;
    $z-index-deepdive: -99999;
*/

// Our custom Z-index variable
$z-index-sticky-header: 9999;
$z-index-rc-trigger-popup: 1000;
$z-index-badge: 10;
$z-index-calender-icon: 8;
$z-index-errors: 6;
$z-index-content-inside-modal: 9002;
$z-index-spinner-overlay: 9040;

html {
  font-family: $salesforce-default-font-family;
  font-size: $salesforce-default-font-size;
}
