.Pill {
  flex: 1 0;

  .Pill__label {
    flex: 1 0;
    margin-left: 0.5em;
    cursor: default;
  }

  &.Pill--truncated {
    .Pill__label {
      max-width: 100px;
    }
  }

  .Pill__icon {
    flex: 0 0 20px;
    margin-right: 0;
  }

  .Pill__remove {
    flex: 0 0 20px;
    width: inherit;
    height: inherit;
    margin-right: 0.25rem;
  }

  .Pill__remove-icon {
    margin: 0;
  }
}
