@import 'lib/package';

$max-button-width: 300px;

.Button {
  &.Button--type-icon-container:focus,
  &.Button--type-icon-bare:focus {
    box-shadow: none;
  }
}

.Button,
.slds-scope .Button {
  max-width: $max-button-width;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;

  &.Button--type-brand:not([disabled]) {
    background-color: $salesforce-blue;
    border-color: $salesforce-blue;
  }

  &.Button--type-outline-brand:not([disabled]) {
    border-color: $salesforce-blue;
  }

  &.Button--type-neutral:not([disabled]) {
    color: $salesforce-blue;
  }

  &.Button--type-neutral-gray {
    padding: 0 1rem;
    text-align: center;
    vertical-align: middle;
    color: $color-gray-10;
    border: 1px solid $color-gray-6;
    transition: border 0.15s linear;
    background-color: $color-gray-4;

    &:disabled {
      background-color: $color-gray-4;
      color: $color-gray-6;
    }

    /* stylelint-disable no-descending-specificity  */
    &:hover {
      background-color: $color-gray-5;
    }
    /* stylelint-enable */
  }

  /* stylelint-disable selector-max-compound-selectors  */
  .Icon.slds-button__icon {
    &.slds-button__icon--x-small {
      width: 0.875rem;
      height: 0.875rem;
    }

    &.slds-button__icon--xx-small {
      width: 0.5rem;
      height: 0.5rem;
    }

    &.slds-button__icon--small {
      width: 1rem;
      height: 1rem;
    }

    &.slds-button__icon--large {
      width: 1.5rem;
      height: 1.5rem;
    }
  }
  /* stylelint-enable */
}
