@import 'lib/package';

.Popover {
  display: inline-block;

  &::before {
    width: 1rem;
    height: 1rem;
    position: absolute;
    transform: rotate(45deg);
    content: '';
    background-color: inherit;
  }
}

.Popover__trigger {
  outline: none;
  display: inline-block;
}

.Popover.Popover__content,
.slds-scope .Popover.Popover__content {
  position: absolute;
  top: 20px;
  z-index: $z-index-popup;

  &.slds-popover {
    border: 1px solid $color-gray-5;
  }
}

.Popover-placement-bottom::before {
  left: 50%;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16);
}
/* stylelint-disable selector-class-pattern  */
.Popover-placement-bottomLeft::before {
  left: 1.5rem;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-bottomRight::before {
  left: auto;
  right: 1rem;
  top: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: -1px -1px 0 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-top::before {
  left: 50%;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-topLeft::before {
  left: 1.5rem;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-topRight::before {
  right: 1rem;
  bottom: -0.5rem;
  margin-left: -0.5rem;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-right::before {
  left: -0.5rem;
  top: 50%;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-rightTop::before {
  left: -0.5rem;
  top: 1.5rem;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-rightBottom::before {
  left: -0.5rem;
  bottom: 1rem;
  margin-top: -0.5rem;
  box-shadow: -1px 1px 2px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-left::before {
  right: -0.5rem;
  top: 50%;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-leftTop::before {
  right: -0.5rem;
  top: 1.5rem;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16);
}

.Popover-placement-leftBottom::before {
  right: -0.5rem;
  bottom: 1rem;
  margin-top: -0.5rem;
  box-shadow: 1px -1px 2px 0 rgba(0, 0, 0, 0.16);
}
/* stylelint-enable */
