@import 'lib/package';

.DuelingPicklist {
  .DuelingPicklist__source-menu,
  .DuelingPicklist__target-menu,
  .DuelingPicklist__target-custom {
    min-height: 210px;
    max-height: 420px;
    box-shadow: none;
  }

  &.DuelingPicklist--menu-block {
    .DuelingPicklist__source-wrapper,
    .DuelingPicklist__target-wrapper {
      // The 44px here is the width of the button column to move things from source to target 
      // or order in target list
      width: calc(50% - 44px);
    }
  }

  .DuelingPicklist__target-custom {
    &.DataGrid {
      height: 15rem;
      border: 1px solid $color-gray-5;
      border-radius: 0.25rem;
      margin-top: 0.125rem;
      margin-bottom: 0.125rem;
    }
  }

  .DuelingPicklist__control {
    flex: 0 0 38px;
    padding: 10px;

    &.DuelingPicklist__control--has-label {
      margin-top: 20px;
    }

    .Button {
      display: block;
      margin: 10px 0;

      &.left {
        margin-left: 0;
      }
    }
  }

  .Menu__list {
    // UI-3474: Fix Double scroll bar issue on picklist with mac settings
    overflow-y: unset;
  }
}

.slds-has-error {
  .DuelingPicklist__source-menu,
  .DuelingPicklist__target-menu,
  .DuelingPicklist__target-custom,
  .OrderingPicklist__list {
    border: 2px solid $color-border-error;
  }

  .DuelingPicklist__source-menu {
    .Input.slds-input {
      border-color: $color-border;
      box-shadow: none;
    }

    .Input.slds-input:active,
    .Input.slds-input:focus {
      outline: 0;
      border-color: $color-brand;
      background-color: $color-background-secondary;
      box-shadow: 0 0 3px $color-stroke-brand;
    }
  }
}
