@import 'lib/package';

.RadioWrapper,
.slds-scope .RadioWrapper {
  width: 100%;
  border: 1px $color-gray-5 solid;
  border-radius: 8px;
  background-color: $color-gray-3;
  padding: 20px 15px;
  margin-bottom: 10px;

  .slds-radio__label {
    display: flex;
  }

  .slds-radio--faux {
    margin-top: 15px;
    flex: 0 0 16px;
  }

  .slds-form-element__label {
    display: flex;
  }
  /* stylelint-disable no-descending-specificity, selector-max-compound-selectors, no-duplicate-selectors */
  .RadioWrapper__icon {
    padding: 0 15px 0 5px;

    .slds-icon-text-default {
      fill: $color-brand;
    }
  }

  .RadioWrapper__icon {
    .slds-icon-text-default {
      fill: $color-gray-5;
    }
  }

  &:hover,
  &.RadioWrapper__checked {
    background-color: $color-background-secondary;

    .slds-icon-text-default {
      fill: $color-brand;
    }
  }

  &.RadioWrapper__disabled {
    background-color: $color-background-dark;

    .slds-icon-text-default {
      fill: $color-gray-5;
    }
  }
  /* stylelint-enable */
  &.RadioWrapper__vertical-middle {
    .slds-radio__label {
      align-items: center;
    }

    .slds-form-element__label {
      align-items: center;
    }

    .slds-radio--faux {
      margin-top: 0;
    }
  }
}
/* stylelint-disable */
.slds-radio [type='radio']:checked,
.slds-scope .slds-radio [type='radio']:checked {
  & + .slds-radio_faux::after,
  & + .slds-radio--faux::after,
  & ~ .slds-radio_faux::after,
  & ~ .slds-radio--faux::after,
  & + .slds-radio__label .slds-radio_faux::after,
  & + .slds-radio__label .slds-radio--faux::after {
    background: $salesforce-blue;
  }
}
/* stylelint-enable */
