.TreeView {
  flex: 1;
  display: flex;
  flex-direction: column;

  .TreeView__Tab {
    height: 46vh;
    padding: 0.5em;
    .Tree__data {
      overflow-y: auto;
      width: max-content;
      overflow-x: auto;
      li {
        // width: calc(100% + 20px);
        // overflow-x: scroll;
      }
    }
  }
  .TreeView__Tab {
    width: auto;
    overflow-x: auto;
  }

  .TeamView__Footer {
    display: flex;
    align-items: center;
    padding: 0.5rem 1em;
    justify-content: space-between;
  }
}

.Tree .Tree__branch {
  overflow-x: auto;
  width: max-content;
}
