@import 'lib/package';

.Tabs {
  .Tab__item {
    white-space: nowrap;
    // salesforce class doesnt seem to exist
    .slds-icon-red {
      fill: $color-background-error;
      margin-left: 4px;
    }
  }

  // disabled and error tab states are new requirements doesn't exist in SLDS
  // needs override to match the mock
  .Tab__item--disabled {
    &:hover,
    &::after {
      background-color: transparent;
    }
  }

  .Tab__link {
    float: left;
  }

  .Tab__anchor--disabled {
    color: $color-gray-7;
    cursor: not-allowed;

    &:hover,
    &:focus {
      color: $color-gray-7;
    }
  }

  .Tab__anchor--error {
    color: $color-background-error;

    &:link,
    &:visited,
    &:hover,
    &:active,
    &:focus {
      color: $color-background-error;
      text-decoration: none;
    }
  }

  .TabItem__overflow {
    .more-tab-scoped {
      width: 70px;
    }

    .more-tab-default {
      width: 60px;
    }

    a {
      text-decoration: none;
    }
  }
}
