@import 'lib/package';

$list-border: 1px solid $color-gray-5;
$list-row-padding: 10px;

.List {
  width: 100%;
  border: $list-border;

  .List__header {
    border-bottom: $list-border;
    background-color: $color-background-reminder;
    padding: $list-row-padding;
  }

  .List__body {
    overflow: auto;
    white-space: nowrap;
  }

  .List__item {
    background-color: $color-gray-1;
    padding: $list-row-padding;

    &:not(:first-child) {
      border-top: $list-border;
    }

    &.List__item--clickable {
      cursor: pointer;

      &:hover {
        background-color: $color-background-reminder;
      }
    }
  }

  .List__footer {
    border-top: $list-border;
    background-color: $color-background-reminder;
    padding: $list-row-padding;
  }
}
