@import 'lib/package';

$card-padding: 10px;
$card-width: 340px;

// card-height based on type
$source-to-target: 400px;
$standalone-edit: 320px;
$standalone-create: 240px;
$checklist: 280px;
$mobile-view: 280px;
$smart-doc: 240px;

.Card {
  width: $card-width;
  background-color: $color-gray-1;
  position: relative;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);

  .Card__header--remove-button {
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
  }

  .Card__header,
  .Card__content {
    padding: $card-padding;
  }

  .Card__header {
    height: 40px;
    text-align: center;
  }

  .Card__content {
    white-space: nowrap;

    .Label {
      display: block;
    }
  }

  .Card__footer {
    .Grid {
      flex-wrap: wrap;
    }

    .Icon {
      margin: 7px 8px;

      &.Card__footer-icon--check {
        fill: $fill-brand;
      }

      &.Card__footer-icon--error {
        fill: $color-background-error-dark;
      }
    }

    .Card__footer-link {
      color: $color-brand;
      padding-right: $card-padding;
    }
  }

  .Card__footer-row {
    flex: 1 0 100%;
    border-top: 1px solid $color-gray-6;
    padding-top: 4px;

    .Text,
    .Icon {
      line-height: 30px;
    }

    .Text.slds-text-body--large {
      font-size: 0.875rem;
    }
  }
}

.Card-content__alias-container,
.Card-content__source-object {
  margin: 10px 0 10px 20px;
}

.Card-content__alias-container {
  .InputWrapper {
    display: inline;
    margin-left: 5px;
  }

  .InputWrapper .Input {
    width: 210px;
  }
}

.NewCard {
  border: 3px dashed $color-gray-6;
  padding: $card-padding;
  width: $card-width;
  background-color: $color-gray-1;
  white-space: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &:hover {
    border: 3px dashed $brand-accessible;
  }

  .NewCard__display {
    color: $color-gray-7;
    font-size: 15px;
    font-style: italic;
    padding: 20px;
    text-align: center;
  }
}

// type card height classes used both in newcard and child/parent cards
.Card--type-source-to-target {
  height: $source-to-target;

  .Card__content {
    height: 240px;
  }

  .Card__footer {
    height: 120px;
  }
}

.Card--type-standalone-edit {
  height: $standalone-edit;

  .Card__content {
    height: 160px;
  }

  .Card__footer {
    height: 120px;
  }
}

.Card--type-standalone-create {
  height: $standalone-create;

  .Card__content {
    height: 160px;
  }

  .Card__footer {
    height: 40px;
  }
}

.Card--type-checklist {
  height: $checklist;

  .Card__content {
    height: 160px;
  }

  .Card__footer {
    height: 80px;
  }
}

.Card--type-mobile-view {
  height: $mobile-view;

  .Card__content {
    height: 160px;
  }

  .Card__footer {
    height: 80px;
  }
}

.Card--type-smart-doc {
  height: $smart-doc;

  .Card__content {
    height: 160px;
  }

  .Card__footer {
    height: 40px;
  }
}
