@import 'lib/package';

.Checkbox {
  .slds-checkbox--faux {
    margin-right: 0.5rem;
  }
}

/* stylelint-disable */
.Checkbox.slds-checkbox [type=checkbox]:checked,
.slds-scope .Checkbox.slds-checkbox [type=checkbox]:checked {
  & + .slds-checkbox_faux::after,
  & + .slds-checkbox--faux::after,
  & ~ .slds-checkbox_faux::after,
  & ~ .slds-checkbox--faux::after,
  & + .slds-checkbox__label .slds-checkbox_faux::after,
  & + .slds-checkbox__label .slds-checkbox--faux::after {
    border-bottom-color: $salesforce-blue;
    border-left-color: $salesforce-blue;
  }
} /* stylelint-enable */
