@import 'lib/package';

.Typeahead {
  .Typeahead__input {
    flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    -webkit-flex-wrap: nowrap;

    .slds-input::placeholder {
      color: $color-text-inverse-weak;
      font-style: italic;
    }
  }

  .Typeahead__menu {
    min-width: unset;
    max-width: unset;
    position: absolute;
    top: 31px;

    &.v-align {
      bottom: 100%;
    }

    &.h-align {
      left: auto;
      right: 0;
    }

    // add one row to the max-height of the menu
    .Menu__list {
      max-height: 340px;
    }

    .Menu__footer {
      background-color: $color-background;
      text-align: center;
      line-height: 1.25;

      /* stylelint-disable */
      .slds-text-heading--label {
        text-transform: none;
        font-weight: bold;
      }
      /* stylelint-enable */
    }
  }

  .TagTypeahead__list {
    margin-top: 5px;
  }
}
