@import 'lib/package';

.Menu__list {
  max-height: 300px;
  overflow-y: auto;
}

.Menu {
  z-index: $z-index-dropdown;
  display: none;
  min-height: 40px;
  padding: 0;

  &.Menu--always-open {
    z-index: auto;
  }

  &.Menu--has-nubbin,
  &.Menu--has-nubbin .Menu__list {
    border: 0;
  }

  .Menu__item {
    user-select: none;

    .Icon.Icon--selected {
      fill: $brand-primary;
    }

    .Menu__item-link {
      justify-content: normal;

      &.Menu__item--is-locked {
        cursor: not-allowed;
      }

      &.Menu__item--is-readonly {
        color: $color-gray-9;
        cursor: not-allowed;
        /* stylelint-disable max-nesting-depth, selector-max-compound-selectors */
        .Icon.Icon--selected {
          fill: $color-gray-10;
        }
        /* stylelint-enable */
      }
    }

    .Menu__item--error-icon {
      display: inline-block;
      vertical-align: bottom;
      line-height: 1;
    }

    // error overflow tab
    // salesforce class doesn't seem to exist
    .slds-icon-red {
      fill: $color-background-error;
      margin-left: 2px;
      margin-top: -10px;
    }

    &.Menu__item--has-secondary .Menu__item-link {
      padding-top: 0.25rem;
      padding-bottom: 0.25rem;
    }

    &.Menu__item--has-focus {
      a {
        background-color: $color-gray-3;
        outline: 0;
        text-decoration: none;
      }
    }

    .Icon {
      flex: none;
    }

    .MenuItem__content {
      flex: 0 1 auto;
    }

    .slds-text-color--weak {
      color: $color-gray-7;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .Menu__item--listbox-option,
    .Menu__item--listbox-option:hover {
      display: block;
    }

    .Menu__item--listbox-option[aria-selected='false'] {
      color: $color-text-weak;
    }
  }

  &.Menu--size-xx-small {
    width: 96px;
    max-width: 96px;
  }

  &.Menu--size-x-small {
    width: 192px;
    max-width: 192px;
  }

  &.Menu--size-small {
    width: 240px;
    max-width: 240px;
  }

  &.Menu--size-medium {
    width: 320px;
    max-width: 320px;
  }

  &.Menu--size-large {
    width: 400px;
    max-width: 400px;
  }

  &.Menu--size-x-large {
    width: 640px;
    max-width: 640px;
  }

  &.Menu--size-xx-large {
    width: 960px;
    max-width: 960px;
  }

  &.Menu--size-block {
    width: 100%;
    max-width: 100%;
  }
}

.Menu__filter {
  margin: 0.25em 1em;

  .Search {
    width: 100%;
    min-width: 10rem;
  }
}

.Menu__footer {
  padding: 0.5rem 0.75rem;

  &.Menu__footer--is-clickable {
    cursor: pointer;
  }
}

.Menu--is-visible {
  display: block;
}
