@import 'lib/package';

.Icon__svmx {
  &.Icon--size-xx-small svg {
    height: 12px;
    width: 12px;
  }

  &.Icon--size-x-small svg {
    height: 16px;
    width: 16px;
  }

  &.Icon--size-small svg {
    height: 24px;
    width: 24px;
  }

  &.Icon--size-medium svg {
    height: 32px;
    width: 32px;
  }

  &.Icon--size-large svg {
    height: 48px;
    width: 48px;
  }

  &.Icon--theme-info .svg-foreground {
    fill: $color-gray-9;
  }

  &.Icon--theme-success .svg-foreground {
    fill: $color-border-success-dark;
  }

  &.Icon--theme-warning .svg-foreground {
    fill: $color-border-warning;
  }

  &.Icon--theme-error .svg-foreground {
    fill: $color-border-error;
  }

  &.Icon--disabled {
    .svg-foreground {
      fill: $color-gray-5;
    }
  }
}

.Button,
.BuilderHeader__item,
.BuilderHeader__nav-item {
  .Icon__svmx .svg-foreground {
    fill: currentColor;
  }
}

.Button {
  .Icon.Icon__svmx {
    .Icon-svg.Icon--size-large-svg {
      height: 24px;
      width: 24px;
    }

    .Icon-svg.Icon--size-small-svg {
      height: 16px;
      width: 16px;
    }

    .Icon-svg.Icon--size-x-small-svg {
      height: 14px;
      width: 14px;
    }

    .Icon-svg.Icon--size-xx-small-svg {
      height: 12px;
      width: 12px;
    }
  }
}
