@import 'lib/package';

.LayoutField {
  display: flex;
  align-items: center;

  .LayoutField__box {
    width: 100%;
    display: flex;
    align-items: center;
    background-color: $color-background-secondary;
    border: 1px solid $color-gray-5;
    border-radius: 0.25rem;
    padding: 0 0.5rem;
    line-height: 1.875rem;
  }

  .LayoutField__close {
    display: none;
  }

  &.LayoutField--is-removable:hover {
    .LayoutField__box {
      width: calc(100% - 30px);
      background-color: $color-gray-3;
    }

    .LayoutField__close {
      display: block;
      cursor: pointer;
      margin-left: 10px;
    }
  }

  &.LayoutField--highlighted {
    outline: auto $color-brand;
  }

  .LayoutField__text {
    flex: 1 0;
    padding-left: 5px;
  }

  &.LayoutField__empty {
    border: 2px $color-border dashed;
    height: 1.875rem;
  }
}
