@import 'lib/package';

.DataGridWithSelection {
  .rt-tr.DataGridWithSelection--is-selected,
  .rt-tr.-odd.DataGridWithSelection--is-selected {
    background-color: $color-background-row-selected;

    .rt-td.DataGrid__cell {
      background-color: transparent; //override cell default for row background above
    }
  }

  .rt-thead .DataGridWithSelection__select-all {
    .rt-resizable-header-content { /* stylelint-disable-line selector-max-compound-selectors */
      text-overflow: initial;
    }
  }
}

.DataGridWithSelection--size-small {
  width: 240px;
  max-width: 240px;
}

.DataGridWithSelection--size-medium {
  width: 320px;
  max-width: 320px;
}

.DataGridWithSelection--size-large {
  width: 400px;
  max-width: 400px;
}

.DataGridWithSelection--size-x-large {
  width: 640px;
  max-width: 640px;
}

.DataGridWithSelection--size-xx-large {
  width: 960px;
  max-width: 960px;
}

.DataGridWithSelection--size-block {
  width: 100%;
  max-width: 100%;
}
