@import 'lib/package';
/* stylelint-disable */
.b-grid-body-container {
  height: 500px;
}

.b-panel-header {
  background-color: $color-gray-1;
  color: $color-gray-13;
}

// VF page has a border that is added
.b-textfield {
  input[type='text'],
  input[type='text']:focus,
  input[type='text']:active {
    border-style: none !important;
    box-shadow: none;
    padding-right: 0.5rem;
  }
}

.b-container {
  .b-has-label.b-field {
    label {
      color: $color-gray-13;
    }
  }
}

.b-popup-content {
  background-color: $color-gray-1;
}

.b-eventeditor.b-popup .b-bottom-toolbar {
  background-color: $color-gray-3;
}

.b-panel-header .b-header-title {
  font-weight: 100;
  font-size: $font-size-7;
  line-height: $line-height-heading;
}

button.b-button {
  color: $color-gray-1;
  text-align: center;

  &.b-green {
    color: $color-gray-1;
    border-color: $salesforce-blue;
    background-color: $salesforce-blue;

    &:hover:not([disabled]) {
      background-color: $salesforce-blue;
      border-color: $salesforce-blue;
    }
  }

  &.b-gray {
    color: $salesforce-blue;
    background-color: $color-gray-1;

    &:hover:not([disabled]) {
      color: $color-brand-dark;
      background-color: $color-gray-2;
    }
  }
}

.b-numberfield.b-contains-focus .b-field-inner,
.b-textfield.b-contains-focus .b-field-inner {
  border-color: $color-gray-6;
}

.b-textfield:not(.b-disabled):hover .b-field-inner,
.b-numberfield:not(.b-disabled):hover .b-field-inner {
  border-color: $salesforce-blue;
}

button.b-button:active:not([disabled]) {
  color: $salesforce-blue;
}

.b-tool {
  color: $color-gray-13;
}

//Header close icon is not on the right
.b-panel-header.b-dock-top {
  display: flex;

  .b-icon.b-popup-close.b-widget.b-tool {
    justify-content: flex-end;
  }
}

//Clock styles on hover
.b-tooltip-content {
  color: $color-gray-8;
}

.b-sch-clock-hour .b-sch-clock {
  border-color: $salesforce-blue;
}

.b-sch-event-title {
  color: $color-gray-13;
}

// Date Picker Styles
.b-datepicker {
  .b-datepicker-header.b-dock-top {
    background-color: $color-gray-1;
    color: $color-gray-13;
    .b-tool {
      color: $color-gray-13; 
    }
  }

  .b-calendar-weekdays {
    background-color: $color-gray-1;
    color: $color-gray-13;
  }

  .b-calendarpanel-content .b-calendar-row.b-calendar-week .b-calendar-cell {
    &:hover,
    &:hover.b-today,
    &.b-selected-date {
      border-color: $salesforce-blue;
      background-color: $salesforce-blue;
    }

    &.b-today {
      color: $color-gray-13;
      border-color: $salesforce-blue;
      background-color: $color-gray-1;
    }
  }
}

.b-panel.b-calendarpanel .b-calendarpanel-content .b-weeks-container {
  background-color: $color-gray-1;
  color: $color-gray-13;
}

.b-datefield.b-open .b-fieldtrigger {
  color: $salesforce-blue !important;
}

// Time input Styles
.b-button.b-blue.b-pressed {
  background-color: $salesforce-blue;
  border-color: $color-gray-6;

  &:hover {
    background-color: $salesforce-blue;
    border-color: $color-gray-6;
  }
}

.b-timefield .b-icon-angle-left:hover,
.b-timefield .b-icon-angle-right:hover {
  color: $salesforce-blue;
}

.b-timefield.b-open .b-icon-clock-live {
  background-color: $salesforce-blue !important;
}

.b-widget.b-container.b-timepicker {
  .b-button.b-blue {
    color: $color-gray-6;
    border-color: $color-gray-6;

    &:hover:not([disabled]) {
      background-color: $salesforce-blue;
    }
  }
}

// Picklist  Styles
.b-list-item {
  color: $color-gray-13;
  border-top-style: none;
}

.b-list-item.b-active:not(.b-disabled),
.b-list-item:focus:not(.b-disabled) {
  background-color: $color-gray-3;
  color: $color-gray-13;
}

.b-list {
  background-color: $color-gray-1;
  color: $color-gray-13;
}

// This style is for the picklist which has a overflow hidden set which is causing a weird bug.
.b-widget:not(.b-overflowing):not(.b-floating):not(.b-list):not(.b-grid):not(.b-subgrid):not(.b-container):not(.b-fieldtrigger):not(.b-card-item):not(.b-badge) {
  overflow: visible;
}
