@import 'lib/package';

.Toggle {
  display: inline-block;
  width: auto;
}

// dirty override until salesforce fix this ticket
// https://success.salesforce.com/issues_view?title=if-api-version-is-higher-than-42-0-brand-color-of-slds-in-vf-is-darker-than-raw-slds&Id=a1p3A000000320Y
/* stylelint-disable selector-class-pattern */
.slds-scope .slds-checkbox_toggle,
.slds-scope .slds-checkbox--toggle {
  width: auto;
}

.slds-scope.slds-checkbox--toggle[type='checkbox']:checked + .slds-checkbox--faux_container .slds-checkbox--faux {
  border-color: $salesforce-blue;
  background-color: $salesforce-blue;

  &:hover,
  &:focus {
    background-color: $salesforce-blue-hover;
  }
}
/* stylelint-enable */
