@import 'lib/package';

.Spinner__container-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $z-index-spinner-overlay;
  background: rgba(0, 0, 0, 0.2);
}

.Spinner__container {
  .slds-spinner_container { /* stylelint-disable-line selector-class-pattern */
    z-index: $z-index-spinner;
  }
}
