.OrderingPicklist {
  display: flex;

  .OrderingPicklist__menu {
    min-height: 210px;
    max-height: 420px;
  }

  &.OrderingPicklist__block {
    .OrderingPicklist__menu {
      flex: 1 0 100%;
      width: 100%;
    }

    .OrderingPicklist__list {
      flex: 1 1;
      // width: 100% is required because slds adds a width of 15rem and on
      // vf pages we see inconsistent width issue with dueling picklist
      width: 100%;
    }
  }

  .OrderingPicklist__control {
    flex: 1 1 100%;
    padding: 10px;

    &.OrderingPicklist__control--has-label {
      margin-top: 20px;
    }

    .Button {
      display: block;
      margin: 10px 0;

      &.left {
        margin-left: 0;
      }
    }
  }

  // UI-3474: Fix Double scroll bar issue on picklist with mac settings
  .Menu__list {
    overflow-y: unset;
  }
}
