@import 'lib/package';

.Tree {
  display: flex;
  height: 100%;
  flex-direction: column;

  .Tree__branch {
    outline: 0;

    .Tree__branch-toggle-icon,
    .Tree__branch-name {
      outline: 0;
    }

    .Tree__branch-toggle-icon {
      margin-top: 0;
    }
  }

  .Tree__data--is-scrollable {
    overflow: auto;
  }

  em {
    font-style: normal;
    background-color: $color-background-highlight-search;
  }
}

.Tree__leaf {
  .item-icon {
    margin-left: 8px;
  }

  .slds-text-color--weak {
    color: $color-gray-9;
  }

  .leaf-data {
    display: flex;
    width: 100%;
    min-width: 0;
  }

  .leaf-action {
    display: flex;
    align-items: center;
    margin: 0 10px;

    .Button {
      align-self: auto;
      margin-top: initial;
      border: 0;

      &:focus {
        box-shadow: none;
      }
    }
  }
}
