@import 'lib/package';
/* stylelint-disable */
.DataGrid {
  min-height: 200px;
  max-height: 100vh;
  background-color: $color-gray-1;
  border-right-width: 0;
  border-left-width: 0;

  &.-no-row-border .rt-tbody .rt-tr-group {
    border-bottom-width: 0;
  }

  &.-col-border {
    .rt-thead .rt-th,
    .rt-tbody .rt-td {
      &:not(:last-child) {
        border-right: 1px solid $brand-light-active;
      }
    }
  }

  &.-col-sortable .rt-thead .rt-th {
    height: 32px;
    padding: 7px 5px;

    /**
    * inner element that gets arrow icon
    * non-resizable column config needs selector div:first-child
    */

    .rt-resizable-header-content,
    div:first-child {
      margin-right: 4px;
      display: inline-block;
      padding: 0 15px 0 0;
    }

    &.DataGrid__header--align-center > div:first-child {
      padding-left: 15px;
    }

    &.-cursor-pointer {
      &:not(.-sort-asc):not(.-sort-desc):hover {
        // header onmousehover + non-resizable column to display sort icon
        .rt-resizable-header-content,
        div[class='']:first-child {
          background: no-repeat 100% center
            url(data:image/svg+xml;base64,);
        }

        // resize handler
        .rt-resizer {
          background-color: $brand-light-active;
        }
      }
    }

    &.-sort-asc {
      // header onclick ascending + non-resizable column to display sort icon
      .rt-resizable-header-content,
      div[class='']:first-child {
        background: no-repeat 100% center
          url(data:image/svg+xml;base64,);
      }

      &:focus {
        border: 1px solid $color-text-link-hover;

        .rt-resizer {
          background-color: $color-text-link-hover;
        }
      }
    }

    &.-sort-desc {
      // header onclick descending + non-resizable column to display sort icon
      .rt-resizable-header-content,
      div[class='']:first-child {
        background: no-repeat 100% center
          url(data:image/svg+xml;base64,);
      }

      &:focus {
        border: 1px solid $color-text-link-hover;

        .rt-resizer {
          background-color: $color-text-link-hover;
        }
      }
    }
  }

  &.DataGrid__list-view {
    .Icon:not(.slds-button__icon) {
      fill: $color-gray-6;
      background-color: transparent;
    }

    .Icon:hover {
      cursor: pointer;
      fill: $color-gray-8;
      background-color: transparent;
    }
  }

  &.DataGrid--fixed-header {
    .rt-table {
      overflow-y: hidden;
    }

    .rt-thead {
      overflow-x: hidden;
      overflow-y: scroll;
    }

    .rt-tbody {
      overflow-x: hidden;
      overflow-y: scroll;
    }
  }

  &.DataGrid--custom-row-colors .rt-tr .rt-td,
  &.DataGrid--custom-row-colors .rt-tr:hover .rt-td,
  &.DataGrid--custom-row-colors.-striped .rt-tr.-odd .rt-td {
    background-color: inherit;
  }

  .rt-tr-group {
    flex: 0 0 auto;
  }

  .rt-resizer {
    right: 0;
    width: 4px;
  }

  .rt-thead.-header,
  .rt-tbody .rt-tr-group {
    border-bottom: 1px solid $brand-light-active;
    box-shadow: none;
  }

  .rt-thead {
    .rt-th.-sort-asc,
    .rt-td.-sort-asc,
    .rt-th.-sort-desc,
    .rt-td.-sort-desc {
      box-shadow: none;
    }

    .rt-tr {
      background-color: $color-gray-2;
    }

    .rt-th {
      display: flex;
      padding: 0.75rem 5px;
      text-transform: uppercase;
      text-align: left;
    }
  }

  .rt-tbody {
    position: relative;
    overflow: initial;

    .rt-td {
      display: flex;
      padding: 5px;
      min-height: 30px;

      .Popover {
        .Popover__trigger {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
      }

    }
  }

  .rt-th {
    outline: none;
    letter-spacing: 0.0625rem;
  }

  .rt-tr {
    align-items: center;
  }

  .rt-thead .rt-th,
  .rt-tbody .rt-td {
    border-right: 0;
    padding: 4px 5px;

    &:first-child {
      padding-left: 25px;
    }

    &:last-child {
      padding-right: 25px;
    }
  }

  .DataGrid__cell--align-center,
  .DataGrid__header--align-center {
    justify-content: center;
  }

  .DataGrid__cell--align-right,
  .DataGrid__header--align-right {
    justify-content: flex-end;
  }

  &.DataGrid--display-density-medium {
    .DataGrid__header, .DataGrid__cell {
      height: 30px;
      align-items: center;
      padding-top: 0;
      padding-bottom: 0;
    }
  }
  
  &.DataGrid--display-density-large {
    .DataGrid__header, .DataGrid__cell {
      height: 47px;
      align-items: center;
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  &.-col-sortable.DataGrid--display-density-large .rt-thead .rt-th {
    height: 47px;
    padding-top: 0;
    padding-bottom: 0;
  }

  &.-col-sortable.DataGrid--display-density-medium .rt-thead .rt-th {
    height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  
  // remove Badge/Badgelist margin rendered inside datagrid
  .BadgeList {
    margin-top: 0;

    .Badge {
      display: inherit;
      margin-bottom: 0;
    }
  }

  // overrides input to be rendered inside datagrid
  .InputWrapper .Input.slds-input {
    min-height: 1.5625rem; // equivalent to 25px Badge/Badgelist
    line-height: 1.5625rem;
  }

  // override line#24,25 .rt-th height/margin and not impact default type `variable` displayDensity
  &.-col-sortable.DataGrid--display-density-medium {
    .rt-thead {
      .rt-th {
        height: 30px;
        margin: 0;
      }
    }
  }

  &.-col-sortable.DataGrid--display-density-large {
    .rt-thead {
      rt-th {
        height: 47px;
        margin: 0;
      }
    }
  }

  .pagination-bottom {
    background-color: $color-gray-2;

    .-pagination {
      width: 350px;
      position: relative;
      margin: 0 auto;
      box-shadow: none;
      border-top: 0;
      justify-content: normal;

      .-center {
        .-pageInfo .-pageJump input {
          width: 45px;
          height: 25px;
          margin-right: 3px;
        }

        .-pageSizeOptions {
          margin: 0;
          position: absolute;
          right: 0;
          margin-right: 30px;

          &:before {
            content: 'Rows';
            padding-right: 10px;
            font-weight: bold;
          }

          select {
            width: 60px;
            height: 30px;
          }
        }
      }

      .-previous {
        margin-right: 10px;

        .-btn:before {
          width: 0;
          height: 0;
          border-right: solid 6px $salesforce-blue;
          border-bottom: solid 6px transparent;
          border-top: solid 6px transparent;
          content: '';
          display: block;
          margin: 0 auto;
        }
      }

      .-next {
        margin-left: 10px;

        .-btn:before {
          content: '';
          width: 0;
          height: 0;
          border-left: solid 6px $salesforce-blue;
          border-bottom: solid 6px transparent;
          border-top: solid 6px transparent;
          display: block;
          margin: 0 auto;
        }
      }

      .-previous,
      .-next,
      .-center {
        flex: none;
      }

      .-previous .-btn,
      .-next .-btn {
        height: 32px;
        width: 30px;
        border: 1px solid $color-gray-5;
        border-radius: 4px;
        background-color: white;
      }
    }
  }
}
