@import 'lib/package';

.InputWrapper__has-icon {
  &.slds-input-has-icon--left-right .slds-input__icon--right {
    left: auto;
  }

  &.slds-input-has-icon--right .slds-input__icon--right {
    right: 0.75rem;
  }
}

.InputWrapper {
  &.slds-size--block {
    width: 100%;
  }
}

.Input {
  &::placeholder {
    font-style: italic;
    color: $color-text-tertiary;
  }

  &::-ms-clear {
    display: none;
  }
}
