@import 'lib/package';
$section-border: 1px $color-gray-4 solid;

.Section {
  .Section__title {
    position: relative;

    .slds-button:focus {
      box-shadow: none;
    }
  }

  .Section__title--error {
    position: absolute;
    top: -20px;
    right: 0;
    z-index: $z-index-errors;

    .Icon {
      color: $color-text-error;
    }
  }

  &.slds-is-open .Section__content {
    overflow: visible;
  }

  &.Section__bordered {
    background-color: $color-background-primary;
    border: $section-border;

    .Section__title {
      border-radius: 0;
      border-bottom: $section-border;

      .slds-button.slds-section__title-action {
        background-color: inherit;
        cursor: default;
      }
    }

    .Section__content {
      padding-bottom: 12px;
    }
  }

  &.Section--highlighted {
    border: 2px solid $color-brand;
  }

  &.Section--error {
    border: 2px solid $color-border-error;
  }
}

.Section__title--action {
  display: flex;
  flex: 1 0;
  justify-content: space-between;
  overflow: hidden;
  outline: 0;

  > span {
    display: flex;
    align-items: center;

    .slds-section__title-action-icon {
      flex: 0 0 14px;
    }

    .Section__title--text {
      white-space: nowrap;
    }

    .Section__subtitle {
      font-size: 14px;
      font-weight: 200;
      color: $color-gray-8;
    }
  }
}
